import { useState } from 'react';

import DeleteIcon from '@mui/icons-material/Delete';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogTitle from '@mui/material/DialogTitle';
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import { useDispatch } from 'react-redux';

import { useNavigation } from '../../../hooks/use-navigation';
import { BaseButton } from '../../base-button';
import { updateItemContent, updateItemTitle } from '../store/slice';

import { StyledItem } from './styled';

interface PostItemProps {
  id: string;
  title: string;
  image: string;
  content: string;
}

export const PostItem = ({ id, image, title, content }: PostItemProps) => {
  const { goToPosts } = useNavigation();

  const [editedContent, setEditedContent] = useState(content);
  const [editedTitle, setEditedTitle] = useState(title);
  const [openDialog, setOpenDialog] = useState(false);

  const dispatch = useDispatch();

  const handleTitleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setEditedTitle(event.target.value);
  };

  const handleContentChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setEditedContent(event.target.value);
  };

  const handleSaveTitleClick = () => {
    dispatch(updateItemTitle({ id, title: editedTitle }));
    console.log('Saved:', id, editedTitle);
  };

  const handleSaveContentClick = () => {
    dispatch(updateItemContent({ id, content: editedContent }));
    console.log('Saved:', id, editedContent);
  };

  const handleCancelClick = () => {
    goToPosts();
  };

  const handleDeleteDialogOpen = () => {
    setOpenDialog(true);
  };

  const handleDeleteDialogClose = () => {
    setOpenDialog(false);
  };

  return (
    <StyledItem data-id={id} maxWidth="lg">
      <img src={image} alt={`Blog Picture-${id}`} />
      <TextField
        fullWidth
        multiline
        value={editedTitle}
        onChange={handleTitleChange}
        variant="outlined"
        sx={{ mb: 2 }}
        InputProps={{
          style: { fontSize: '24px' },
        }}
      />
      <Stack
        direction="row"
        spacing={2}
        sx={{
          justifyContent: 'flex-end',
          mb: 3,
        }}
      >
        <BaseButton onClick={handleSaveTitleClick}>Save</BaseButton>
      </Stack>
      <TextField
        fullWidth
        multiline
        rows={10}
        value={editedContent}
        onChange={handleContentChange}
        variant="outlined"
        sx={{ mb: 2 }}
      />
      <Stack
        direction="row"
        spacing={2}
        sx={{
          justifyContent: 'flex-end',
          mb: 5,
        }}
      >
        <BaseButton onClick={handleSaveContentClick}>Save</BaseButton>
      </Stack>
      <Stack
        direction="row"
        spacing={2}
        sx={{
          justifyContent: 'flex-end',
        }}
      >
        <BaseButton>Save all</BaseButton>
        <BaseButton onClick={handleCancelClick}>Cancel</BaseButton>
        <Button variant="outlined" onClick={handleDeleteDialogOpen} startIcon={<DeleteIcon />}>
          Delete post
        </Button>
        <Dialog open={openDialog} onClose={handleDeleteDialogClose} aria-labelledby="delete-dialog">
          <DialogTitle id="delete-dialog">{'Do you want to delete this post?'}</DialogTitle>
          <DialogActions>
            <Button autoFocus onClick={handleDeleteDialogClose}>
              No
            </Button>
            <Button onClick={handleDeleteDialogClose} autoFocus>
              Delete
            </Button>
          </DialogActions>
        </Dialog>
      </Stack>
    </StyledItem>
  );
};
